.overlay{position:fixed;display:none;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;left:0;top:0;width:100%;height:100%;background-image:url(http://7lrzqf.com1.z0.glb.clouddn.com/image/cropped-staraw2.jpg);background-size:cover;background-attachment:fixed;background-position:top center;background-color:#4c5766;z-index:9999}.overlay_open,.overlay_open body{overflow:hidden}.overlay_open .overlay{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.overlay_mask{position:absolute;left:0;top:0;width:100%;height:100%;z-index:10;background:rgba(0,5,8,0.9)}.overlay_content{position:relative;box-sizing:border-box;padding-left:10px;padding-right:10px;z-index:22;-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1}.overlay [slot="overlay_hide"]{position:absolute}



